<template>

  <div id="crea-box">


    <el-input type="textarea" v-model="text" size="medium" autosize="{ minRows: 2, maxRows: 10 }">{{ text }}</el-input>

    <el-button type="button" @click="next()">
      下一条
    </el-button>
    <el-button type="button" @click="add()">
      添加
    </el-button>


  </div>


</template>

<script>
import wxapi from "@/api/api.js"

export default {
  name: "crea",
  data() {
    return {
      text: "默认显示"
    }
  },
  created() {
    this.next();
  },
  methods: {
    async next() {
      let re = await wxapi.creatCaihongp();
      this.text = re;
    },
    add() {
      wxapi.addEntity(this.text)
    }


  }
}
</script>

